// 3rd Party
// Redux
import { currencyformat, storeSettings } from "@/redux/slices/userSlice";

//Seller list api
export async function getsellerDatalist(
  slug,
  setSellerList,
  setLoader,
  offset,
) {
  setLoader(false);
  const result = await axiosInstance.get(
    `vendor-store/category-based-vendor-list?categorySlug=${slug}&limit=10&offset=${offset}`,
  );
  if (result && result?.data?.status == 1) {
    setLoader(true);
    setSellerList(result?.data?.data);
  }
}

//Seller list count api
export async function getsellerDatalistcount(slug, setTotalCount) {
  const result = await axiosInstance.get(
    `vendor-store/category-based-vendor-list?categorySlug=${slug}&count=1`,
  );
  if (result && result?.data?.status == 1) {
    setTotalCount(result?.data?.data);
  }
}

