//Language list api
export async function contactusapi(dispatch, setStoreSettings) {
  const result = await axiosInstance.get(`settings/store-setting`);
  if (result?.data?.status == 1) {
    Cookieslanguage(result?.data?.data?.[0]?.defaultLanguageId);
    CookiesDescription(result?.data?.data?.[0]?.storeDescription);
    setStoreSettings(result?.data?.data?.[0]);
    const check = datacheck?.find(
      (obj) => obj.code == result?.data?.data?.[0]?.currencyCode,
    );
    localStorage.setItem("settings", JSON.stringify(result?.data?.data?.[0]));
    localStorage.setItem(
      "languageId",
      result?.data?.data?.[0]?.defaultLanguageId,
    );
    localStorage.setItem(
      "bussinessName",
      result?.data?.data?.[0]?.businessName,
    );
    dispatch(storeSettings(result?.data?.data?.[0]));

    if (check == undefined) {
      dispatch(currencyformat({ symbol: "$", text: "USD", position: "left" }));
    } else {
      dispatch(
        currencyformat({
          symbol: result?.data?.data?.[0]?.symbolLeft,
          text: result?.data?.data?.[0]?.currencyCode,
          position:
            result?.data?.data?.[0]?.symbolRight !== null ? "right" : "left",
        }),
      );
    }
  }
}

//Addons api
export async function addonapi() {
  const result = await axiosInstance.get(`list/addons`);
  if (result?.data) {
    CookiesetAddon(result?.data?.data);
    localStorage.setItem("addon", JSON.stringify(result?.data?.data));
  }
}

