export async function FailedOrderListApi(
  setFailedOrderList,
  setLoader,
  search,
  offset,
) {
  const result = await axiosInstance.get(
    `orders/failed?limit=5&offset=${
      offset ? offset : 0
    }&count=0&keyword=${search}`,
  );
  if (result?.data?.status == 1) {
    const failedList = await Promise.all(
      result.data.data.map(async (order) => {
        const productList = await axiosInstance.get(
          `orders/${order?.orderId}/order-products`,
        );
        order.productList = productList.data.data;
        return order;
      }),
    );
    setFailedOrderList(failedList);
    setLoader(true);
  }
}

export async function FailedOrderCountApi(setValue, setLoader, search) {
  const result = await axiosInstance.get(
    `orders/failed?limit=5&offset=0&count=1&keyword=${search}`,
  );
  if (result?.data?.status == 1) {
    setValue(result?.data?.data);
    setLoader(true);
  }
}
