export async function invoicedownload(token, id) {
  const downloadFile = `${baseUrl}orders/order-export-pdf?orderProductId=${id}`;
  const filename = "Invoice" + Date.now() + ".pdf";

  const headers = {
    Authorization: `Bearer ${token} `,
    key: storeKey,
    "Content-Type": "application/pdf",
    Accept: "application/json, text/plain,",
  };
  const config = {
    method: "GET",
    headers,
  };
  const response = await fetch(downloadFile, config);
  if (response?.status == 200) {
    const blob = await response.blob();
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.download = filename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(link.href);
  }
}

export async function cancelreasonApi(cancelOrder, id, reason, router) {
  const data = {
    description: reason,
    orderProductId: Number(id),
    reasonId: cancelOrder,
  };
  const result = await axiosInstance.post(`orders/order-cancel-request`, data);
  if (result?.data?.status == 1) {
    router.push("/account-settings/order-history");
  }
}

export async function cancelReasonApi(setCancelReason) {
  const result = await axiosInstance.get(
    "orders/order-cancel-reason-list?limit=0&offset=0&count=0",
  );

  if (result && result?.data && result?.data?.data) {
    const reasonopt =
      result?.data?.data &&
      result?.data?.data?.map((data) => ({
        label: data.reason,
        value: data.id,
      }));

    setCancelReason(reasonopt);
  }
}

