import { getWishListCount } from "@/redux/slices/userSlice";
import ToastMessage from "@/Components/ToastMessage";
import axiosInstance from "../interceptor";
import { baseUrl, storeKey } from "@/utilities/url";

export async function Productsearch(
  e,
  setProductsearch,
  setProductSearchOffset,
  setProductLoader
) {
  const result = await axiosInstance.get(
    `product-store/product-search-list?keyword=${e}&limit=10&offset=0`,
  );
  if (result && result.data.status == 1) {
    setProductsearch(result.data.data);
    setProductLoader(false)
    setProductSearchOffset(result?.data?.data);
  }
}

export async function Productsearchpaginate(
  e,
  setProductsearch,
  offset,
  setProductSearchOffset,
  setScrollLoader,
) {
  const result = await axiosInstance.get(
    `product-store/product-search-list?keyword=${e}&limit=10&offset=${offset}`,
  );
  if (result && result.data.status == 1) {
    setProductsearch((previousData) => [
      ...previousData,
      ...result?.data?.data,
    ]);
    setProductSearchOffset(result?.data?.data);
    setScrollLoader(true);
  }
}

export async function Productfilters(
  name,
  manuSubArr,
  setProductdetails,
  setLoader,
  offset,
  variantmenuSubArr,
  order,
  mPrices,
  lPrice,
  dispatch,
  categoryname,
) {
  const result = await axiosInstance.get(
    `list/custom-product-list?categorySlug=${name}&attribute=${manuSubArr}&limit=10&offset=${offset}&variant=${variantmenuSubArr}&price=${
      order?.price ? order?.price : ""
    }&priceFrom=${lPrice}&priceTo=${mPrices}&latestArrival=${
      order?.latestArrival ? order?.latestArrival : ""
    }`,
  );
  if (result && result.data.status == 1) {
    setProductdetails(result.data?.data);
    setLoader(true);
    // Finding category name for dispatching
    const res = result?.data?.categoryLevel?.find(
      (value) => value?.categorySlug == result?.data?.categorySlug,
    );
    dispatch(categoryname(res?.categoryName));
  }
}

