export const productCompareApi = async (
  data,
  compareIds,
  setCompareTrigger,
  comparetrigger,
  productId,
) => {
  const result = await axiosInstance.get(
    "product-store/product-compare?data=" + data + "&productId=" + compareIds,
  );
  if (result?.data?.status == 1) {
    if (result?.data?.message == "Please Choose Products from Same Category") {
      ToastMessage({ type: "error", message: result.data.message });
      localStorage.setItem("compareId", JSON.stringify(productId));
    } else {
      localStorage.setItem("compareId", JSON.stringify(compareIds));
      setCompareTrigger(comparetrigger + 1);
      ToastMessage({ type: "success", message: result.data.message });
    }
  }
};

export const ProductCompareListApi = async (
  data,
  productId,
  setCompareProductList,
  setLoader,
) => {
  const result = await axiosInstance.get(
    "product-store/product-compare?data=" + data + "&productId=" + productId,
  );
  if (result?.data?.status == 1) {
    setCompareProductList(result?.data?.data);
    localStorage.setItem("compareId", JSON.stringify(productId));
    setLoader(true);
  }
};

export const WishListDeleteApi = async (id, dispatch, wishCount) => {
  const result = await axiosInstance.delete(
    `/customer/wishlist-product-delete/${id}`,
  );
  if (result?.data?.status == 1) {
    dispatch(getWishListCount(wishCount - 1));
    ToastMessage({ type: "success", message: result?.data?.message });
    return result;
  }
};

export const AddWishListApi = async (id, dispatch, wishCount) => {
  let requestData = {
    productId: id,
    productOptionValueId: "",
  };
  const result = await axiosInstance.post(
    `/customer/add-product-to-wishlist`,
    requestData,
  );
  if (result?.data?.status == 1) {
    dispatch(getWishListCount(wishCount + 1));
    ToastMessage({ type: "success", message: result?.data?.message });
    return result;
  }
};

