const EmailValidator = (value) => {
  if (/^\w([\\.-]?\w)*@\w([\\.-]?\w)*(\.\w{2,3})+$/.test(value)) return true;
  else return false;
};

const BloodGroupValidator = (value) => {
  if (/^(A|B|AB|O)[+-]$/.test(value)) {
    return true;
  } else {
    return false;
  }
};

const exceptThisSymbols = ["e", "E", "-", "."];
const exceptspace = [" "];
const exceptThisZero = ["0"];

const NumberValidator = (value) => {
  if (/^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\\./0-9]*$/.test(value)) {
    return true;
  } else {
    return false;
  }
};
const PhonenoValidator = (value) => {
  if (/^\+?\d{2}(\s?\d){5,}$/.test(value)) {
    return true;
  } else {
    return false;
  }
};
const PaginationValidator = (value) => {
  var roleExpression = /[0]/;
  var regex = new RegExp(roleExpression);
  var t = value;
  if (!t.match(regex)) {
    return true;
  } else {
    return false;
  }
};
export {
  EmailValidator,
  NumberValidator,
  BloodGroupValidator,
  PhonenoValidator,
};

const upperPresent = (value) => {
  if (/^(?=.*[A-Z]).+$/.test(value)) return true;
  else return false;
};

const lowerPresent = (value) => {
  if (/^(?=.*[a-z]).+$/.test(value)) return true;
  else return false;
};

const numPresent = (value) => {
  if (/\d/.test(value)) return true;
  else return false;
};

const specialPresent = (value) => {
  if (/([!@#$%^&*(),.?":{}|<>])/.test(value)) return true;
  else return false;
};
const GstValidator = (value) => {
  if (/^[0-9]{2}[A-Z]{5}[0-9]{4}[A-Z]{1}[1-9A-Z]{1}Z[0-9A-Z]{1}$/.test(value))
    return true;
  else return false;
};
const PanValidator = (value) => {
  if (/[A-Z]{5}[0-9]{4}[A-Z]{1}/.test(value)) return true;
  else return false;
};
const IfscValidator = (value) => {
  if (/^[^\s]{4}\d{7}$/.test(value)) return true;
  else return false;
};

const AccountNumberValidator = (value) => {
  if (/^\d{9,18}$/.test(value)) return true;
  else return false;
};

const BicValidator = (value) => {
  if (/^[A-Z]{6}[A-Z0-9]{2}([A-Z0-9]{3})$/.test(value)) return true;
  else return false;
};

const PincodeValidator = (value) => {
  if (/^\d*(?:[.,]\d{1,2})?$/.test(value)) return true;
  else return false;
};

const NumberOnlyValidator = (value) => {
  if (/^\d*(?:[.,]\d{1,2})?$/.test(value)) return true;
  else return false;
};

const URLValidator = (value) => {
  if (
    /(([\w]+:)?\/\/)?(([\d\w]|%[a-fA-f\d]{2,2})+(:([\d\w]|%[a-fA-f\d]{2,2})+)?@)?([\d\w][-\d\w]{0,253}[\d\w]\.)+[\w]{2,63}(:[\d]+)?(\/([-+_~.\d\w]|%[a-fA-f\d]{2,2})*)*(\?(&?([-+_~.\d\w]|%[a-fA-f\d]{2,2})=?)*)?(#([-+_~.\d\w]|%[a-fA-f\d]{2,2})*)?/.test(
      value
    )
  )
    return true;
  else return false;
};

const DecimalValidator = (value) => {
  if (/^\d*\.?\d{0,2}$/.test(value)) return true;
  else return false;
};

const NameValidator = (value) => {
  if (/^[a-zA-Z\s]*$/.test(value)) return true;
  else return false;
};

const CompanyNameValidator = (value) => {
  if (/^[a-zA-Z0-9 $,'&-]*$/.test(value)) return true;
  else return false;
};

export {
  upperPresent,
  lowerPresent,
  numPresent,
  specialPresent,
  exceptThisZero,
  exceptspace,
  exceptThisSymbols,
  PaginationValidator,
  PanValidator,
  GstValidator,
  IfscValidator,
  AccountNumberValidator,
  BicValidator,
  PincodeValidator,
  NumberOnlyValidator,
  URLValidator,
  DecimalValidator,
  NameValidator,
  CompanyNameValidator,
};
