import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  CheckoutPaymentMethod: 0,
  checkoutCouponData: "",
  couponDiscountValue: 0,
  couponCode: "",
  enableBackOrder: false,
  enableFailedOrder: false,
  enteredCouponCode: "",
  couponApplied: false,
  cartItems: "",
  totalPriceWithoutTax: 0,
  totalPriceWithTax: 0,
  finalShippingAddress: null,
  finalBillingAddress: null,
  finalCartItems: [],
  loggedOutCheckout: {},
  failedOrderCheckout: {},
  deliveryAsBillingAddressRedux: false,
  loggedOutAddressRedux: null,
};

export const CheckoutRedux = createSlice({
  name: "Variant",
  initialState,
  reducers: {
    CheckoutPaymentMethod: (state, action) => {
      state.CheckoutPaymentMethod = action.payload;
    },
    checkoutCouponData: (state, action) => {
      state.checkoutCouponData = action.payload;
    },
    couponDiscountValue: (state, action) => {
      state.couponDiscountValue = action.payload;
    },
    couponCode: (state, action) => {
      state.couponCode = action.payload;
    },
    enableBackOrder: (state, action) => {
      state.enableBackOrder = action.payload;
    },
    enableFailedOrder: (state, action) => {
      state.enableFailedOrder = action.payload;
    },
    enteredCouponCode: (state, action) => {
      state.enteredCouponCode = action.payload;
    },
    couponApplied: (state, action) => {
      state.couponApplied = action.payload;
    },
    checkoutItems: (state, action) => {
      state.cartItems = action.payload;
    },
    checkoutPriceWithoutTax: (state, action) => {
      state.totalPriceWithoutTax = action.payload;
    },
    checkoutPriceWithTax: (state, action) => {
      state.totalPriceWithTax = action.payload;
    },
    selectedShippingAddress: (state, action) => {
      state.finalShippingAddress = action.payload;
    },
    selectedBillingAddress: (state, action) => {
      state.finalBillingAddress = action.payload;
    },
    selectedCartItems: (state, action) => {
      state.finalCartItems = action.payload;
    },
    loggedOutCheckoutObj: (state, action) => {
      state.loggedOutCheckout = action.payload;
    },
    failedOrderCheckoutObj: (state, action) => {
      state.failedOrderCheckout = action.payload;
    },
    setDeliveryAsBillingAddressRedux: (state, action) => {
      state.deliveryAsBillingAddressRedux = action.payload;
    },
    setLoggedOutAddressRedux: (state, action) => {
      state.loggedOutAddressRedux = action.payload;
    },
  },
});

export const {
  CheckoutPaymentMethod,
  checkoutCouponData,
  couponDiscountValue,
  couponCode,
  enableBackOrder,
  enableFailedOrder,
  enteredCouponCode,
  couponApplied,
  checkoutItems,
  checkoutPriceWithoutTax,
  checkoutPriceWithTax,
  selectedShippingAddress,
  selectedBillingAddress,
  selectedCartItems,
  loggedOutCheckoutObj,
  failedOrderCheckoutObj,
  setDeliveryAsBillingAddressRedux,
  setLoggedOutAddressRedux,
} = CheckoutRedux.actions;

export default CheckoutRedux.reducer;
