"use client";
import { getWishListCount, viewCartCount } from "@/redux/slices/userSlice";
import ToastMessage from "@/Components/ToastMessage";
import { AddToCartLocal } from "@/utilities/AddToCart";
import axiosInstance from "../interceptor";

export async function addToCartApi(
  quantity,
  type,
  datacart,
  dispatch,
  method,
  setBtnLoader,
  router
) {
  const data = JSON.stringify({
    productId: datacart?.productId,
    productPrice: datacart?.price,
    quantity: quantity,
    type: type,
    productOptionValueId: "",
    productVarientOptionId: "",
    varientName: "",
    skuName: datacart?.skuName,
    tirePrice: "",
    vendorId: datacart?.vendorId,
  });
  const result = await axiosInstance.post("cart", data);
  if (result && result.data && result.data.status === 1) {
    if (method == "buynow") {
      router.push("/checkout");
    }
    setBtnLoader(false);
    result.data.data.quantity = parseInt(
      datacart?.minQuantityAllowedCart ? datacart?.minQuantityAllowedCart : 1
    );
    AddToCartLocal(datacart, dispatch);
    ToastMessage({ type: "success", message: result.data.message });
  } else {
    setBtnLoader(false);
    ToastMessage({ type: "error", message: result.data.message });
  }
}

export async function cartListApi(dispatch) {
  const result = await axiosInstance.get(`cart`);

  if (result && result.data && result.data.data) {
    result.data.data.cartList?.map((data) => {
      data.productCount = data.quantity;
      data.productPrice = data.price;
      data.priceGroup = 0;
    });
    localStorage.setItem(
      "cartItems",
      JSON.stringify(result.data.data.cartList)
    );
    dispatch(viewCartCount(result.data.data.cartList));
  }
}

export async function removeFromCartApi(
  id,
  price,
  skuName,
  cartdata,
  setTrigger,
  dispatch,
  skuId
) {
  const data = JSON.stringify({
    productId: id,
    productPrice: price,
    quantity: 0,
    productOptionValueId: "",
    type: "new",
    optionValueName: "",
    productVarientOptionId: "",
    varientName: "",
    skuName: skuName,
  });
  const result = await axiosInstance.post("cart", data);
  if (result && result.data && result.data.status === 1) {
    const cartlist = cartdata.filter((item) => item?.skuId !== skuId);
    localStorage.setItem("cartItems", JSON.stringify(cartlist));
    dispatch(viewCartCount(cartlist));
    setTrigger((pre) => pre + 1);
    ToastMessage({ type: "success", message: result.data.message });
  }
}

export async function wishiListApi(setWishListCount, dispatch, offset) {
  const result = await axiosInstance.get(
    `customer/wishlist-product-list?limit=5&offset=${offset ? offset : 0}`
  );

  if (result && result.data && result.data.data) {
    setWishListCount(result.data.data);
    if (result.data.data?.length == 0) {
      dispatch(getWishListCount(0));
    }
  }
}

export async function wishiListTotalCountApi(setWishListCount, dispatch) {
  const result = await axiosInstance.get(
    `customer/wishlist-product-list?limit=0&offset=0`
  );

  if (result && result.data && result.data.data) {
    setWishListCount(result.data.data);
    dispatch(getWishListCount(result.data.data?.length));
  }
}

export async function wishiListApiCount(setwishListapicount, dispatch) {
  const result = await axiosInstance.get(
    "customer/wishlist-product-list?limit=0&offset=0&count=1"
  );

  if (result && result.data && result.data.data) {
    setwishListapicount(result.data.data);
    dispatch(getWishListCount(result?.data?.data));
  }
}

export const wishListDeleteApi = async (
  id,
  dispatch,
  wishCount,
  handWishListCount,
  trriger,
  setTrriger
) => {
  const data = await axiosInstance.delete(
    `/customer/wishlist-product-delete/${id}`
  );
  if (data?.data?.status == 1) {
    setTrriger(trriger + 1);
    wishiListApi(handWishListCount, dispatch);
    ToastMessage({ type: "success", message: data?.data?.message });
  }
};

export async function CheckoutCartListApi(
  dispatch,
  setCartdata,
  setTrigger,
  trigger,
  addonlist,
  setProductPricingGroupList
) {
  const result = await axiosInstance.get(`cart`);
  if (result && result.data && result.data.data) {
    if (
      addonlist?.["product-price-group"] &&
      addonlist?.["product-price-group"]?.status == 1
    ) {
      const pricingGroupPromises = result.data.data.cartList.map(
        async (product) => {
          const pricingGroup = await PriceGroupListApi(product?.skuId);
          return pricingGroup;
        }
      );
      const pricingGroups = await Promise.all(pricingGroupPromises);
      let pricingGroupList = [...pricingGroups.flat()];
      setProductPricingGroupList(pricingGroupList);
      if (pricingGroupList?.length > 0) {
        const updatedCartData = result.data.data.cartList.map((data) => {
          const priceGroup = pricingGroupList?.filter(
            (product) => product?.skuId == data?.skuId
          );
          if (priceGroup?.length > 0) {
            const tirePrice = priceGroup?.filter(
              (price) => price?.maxQuantity <= data.quantity
            );
            const newPrice =
              tirePrice?.length > 0 ? tirePrice.at(-1)?.price : data.price;
            const priceGroups = tirePrice?.length > 0 ? 1 : 0;
            const normalPrice = data.price;
            return {
              ...data,
              productPrice: normalPrice,
              price: newPrice,
              priceGroup: priceGroups,
              productCount: data.quantity,
            };
          } else {
            return {
              ...data,
              productCount: data.quantity,
              productPrice: data.price,
              priceGroup: 0,
            };
          }
          return data;
        });

        const finalData = await Promise.all(updatedCartData);
        setCartdata(finalData);
        dispatch(viewCartCount(finalData));
        localStorage.setItem("cartItems", JSON.stringify(finalData));
        setTrigger(trigger + 1);
      } else {
        result.data.data.cartList?.map((data) => {
          data.productCount = data.quantity;
          data.productPrice = data.price;
          data.priceGroup = 0;
        });
        localStorage.setItem(
          "cartItems",
          JSON.stringify(result.data.data.cartList)
        );
        setCartdata(result.data.data.cartList);
        dispatch(viewCartCount(result.data.data.cartList));
        setTrigger(trigger + 1);
      }
    } else {
      result.data.data.cartList?.map((data) => {
        data.productCount = data.quantity;
        data.productPrice = data.price;
        data.priceGroup = 0;
      });
      localStorage.setItem(
        "cartItems",
        JSON.stringify(result.data.data.cartList)
      );
      setCartdata(result.data.data.cartList);
      dispatch(viewCartCount(result.data.data.cartList));
      setTrigger(trigger + 1);
    }
  }
}

export async function PriceGroupListApi(sku) {
  const result = await axiosInstance.get(`price-group-detail/sku/${sku}`);
  if (result?.data?.status === 1) {
    const sortValue = result.data.data?.sort(
      (a, b) => a.maxQuantity - b.maxQuantity
    );
    return result.data.data;
  }
}

export async function CartIncreaseAndDecreaseApi(datacart) {
  const data = JSON.stringify({
    productId: datacart?.productId,
    productPrice: datacart?.price,
    quantity: datacart?.productCount,
    productOptionValueId: "",
    productVarientOptionId: "",
    varientName: "",
    skuName: datacart?.skuName,
    tirePrice: "",
    vendorId: datacart.vendorId,
  });
  const result = await axiosInstance.post("cart", data);
  if (result && result.data && result.data.status === 0) {
    ToastMessage({ type: "error", message: result.data.message });
  }
}
