"use client";

import axiosInstance from "../interceptor"

//edit profile api
export const CustomerProfileDataApi = async (setProfileEditData) => {
  const result = await axiosInstance.get(`customer/get-profile`);
  if (result?.data?.status == 1) {
    setProfileEditData(result?.data?.data);
  }
};

//Address profile api
export const CustomerProfileAddressApi = async (setAddressData) => {
  const result = await axiosInstance.get(`customer-address`);
  if (result?.data?.status == 1) {
    setAddressData(result?.data?.data?.customerAddress);
  }
};
