import axiosInstance from "../interceptor";
import ToastMessage from "@/Components/ToastMessage";
import { failedOrderRetryPaymentHours } from "../../utilities/url";

export const OrderDetailApi = async (params, setOrderData, setLoader) => {
  const result = await axiosInstance.get(
    `orders/order-detail?orderProductId=${params}`
  );
  if (result?.data?.status == 1) {
    setOrderData(result?.data?.data);
    setLoader(false);
  }
};

export const FailedOrderDetailApi = async (
  params,
  setOrderData,
  setEnableRetry
) => {
  const result = await axiosInstance.get(`orders/${params}/order-products`);
  if (result?.data?.status == 1) {
    setOrderData(result?.data?.data);
    const startDate = new Date(result?.data?.data?.at(0)?.createdDate);
    const endDate = new Date(startDate);

    endDate.setHours(startDate.getHours() + +failedOrderRetryPaymentHours);
    const currentDate = new Date();
    if (currentDate >= startDate && currentDate < endDate) {
      setEnableRetry(false);
    } else {
      setEnableRetry(true);
    }
  }
};

export async function FailedOrderRetryPaymentApi(
  paymentdata,
  setBtBuyNowLoader,
  router
) {
  const result = await axiosInstance.put(`orders/retry-payment`, paymentdata);

  if (result && result.data.status == 1) {
    router.push("/order-placed");
    ToastMessage({ type: "success", message: result.data.message });
    setBtBuyNowLoader(false);
  } else if (result && result.data.status == 3) {
    router.push("/");
    window.open(result.data.data?.route, "_blank");
    setBtBuyNowLoader(false);
  } else {
    setBtBuyNowLoader(false);
  }
}

export const TrackOrderApi = async (
  productId,
  setTrackData,
  setStatusCheck
) => {
  const result = await axiosInstance.get(
    `orders/track-order-product?orderProductId=${productId}`
  );
  if (result?.data?.status == 1) {
    setTrackData(result?.data?.data);
    if (
      result?.data?.data &&
      result?.data?.data.deliveryStatus &&
      result?.data?.data.deliveryStatus.length !== 0
    ) {
      let deliveryArray =
        result?.data?.data &&
        result?.data?.data?.deliveryStatus.filter(
          (order, index) => order.createdDate !== "" && setStatusCheck(index)
        );
    }
  }
};
